import { Avatar, Button, Card, Divider, Grid, IconButton, Stack, Tooltip, Typography } from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
import CustomTabs from "../../genericComponents/tabs";
import { addToShortList, getAlllistings, getSingleUniversity } from "../../services";
// import { setPopup } from "../../store/Slices/popupSlice";
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Box,
} from "@mui/material";
