import ThermostatIcon from "@mui/icons-material/Thermostat";
import Images from "../../assets";
import Reviews from "./reviews";
import { ArrowBack, Info } from "@mui/icons-material";
// import RatingComponent from "../university/review";
import { setPopup } from "../../store/Slices/popupSlice";
import { initReviews } from "../../store/Slices/reviewSlice";
// import { setWordCase } from "../../assets/library";
// import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import { useNavigate, useParams } from "react-router-dom";
import FavoriteOutlinedIcon from '@mui/icons-material/FavoriteOutlined';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import { monthNames, months } from "src/assets/menu";
import { globalButton, secondaryButton, singlePageStyles } from "src/components/styles/customStyles";
